import React, { PureComponent } from 'react';
import { Grid, Box } from '@material-ui/core';
import { PieChart, Pie, ResponsiveContainer } from 'recharts';
import renderActiveShape from './Charts';
import renderLabelsShape from './Labels';
const data = [
	{ id: 'purple', value: 230, fill: 'url(#gradient-purple)', stroke: 'none', outerRadius: 100 },
	{ id: 'cyan', value: 300, fill: 'url(#gradient-cyan)', stroke: 'none', outerRadius: 80 },
	{ id: 'till', value: 300, fill: 'url(#gradient-till)', stroke: 'none', outerRadius: 100 },
	{ id: 'empty', value: 100, fill: '#000', stroke: 'none', outerRadius: 80 }
];

export default class Charts extends PureComponent {
	state = {
		activeIndex: 0
	};

	onPieEnter = (data, index) => {
		this.setState({
			activeIndex: index
		});
	};

	render() {
		return (
			<div>
				<Grid justify="center" container>
					<Box pt={3.5} pb={1.5}>Statistic</Box>
				</Grid>
				<br />
				<Grid style={{ width: '100%', height: '450px', textAlign: 'center' }} container justify="center">
					<ResponsiveContainer>
						<PieChart>
							<Pie
								activeIndex={this.state.activeIndex}
								activeShape={renderActiveShape}
								data={data}
								labelLine={false}
								label={renderLabelsShape}
								innerRadius={60}
								outerRadius={80}
								dataKey="value"
								onMouseEnter={this.onPieEnter}
							/>
						</PieChart>
					</ResponsiveContainer>
				</Grid>
			</div>
		);
	}
}
